body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: rgb(0, 0, 0);
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body {
  background: #310866;
  background: -webkit-linear-gradient(right, rgba(255,0,0,0), #360b41);
  background: linear-gradient(to left, rgba(255,0,0,0), #360b41);
  font-family: 'Crimson Text', serif;
}
.jumbotron {
    background-color:#2c0e2c;
    color: rgb(105, 90, 107);
}

i.fa.fa-github.fa-3x,
i.fa.fa-linkedin.fa-3x {
    margin-left: 10px;
    margin-right: 10px;
    color: rgb(187, 159, 194);
    padding: 0.25rem 0;
    font-size: 3rem;
    -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
}

i.fa.fa-github.fa-3x:hover {
    color: #631a63;
    cursor: pointer;
    -webkit-animation-name: bounce;
            animation-name: bounce;
    -moz-animation-name: bounce;
}

i.fa.fa-linkedin.fa-3x:hover {
    color: #531458;
    cursor: pointer;
    -webkit-animation-name: bounce;
            animation-name: bounce;
    -moz-animation-name: bounce;
}

@-webkit-keyframes bounce {
    0%,
    100%,
    20%,
    50%,
    80% {
        -webkit-transform: translateY(0);
        transform: translateY(0)
    }
    40% {
        -webkit-transform: translateY(-30px);
        transform: translateY(-30px)
    }
    60% {
        -webkit-transform: translateY(-15px);
        transform: translateY(-15px)
    }
}

@keyframes bounce {
    0%,
    100%,
    20%,
    50%,
    80% {
        -webkit-transform: translateY(0);
        transform: translateY(0)
    }
    40% {
        -webkit-transform: translateY(-30px);
        transform: translateY(-30px)
    }
    60% {
        -webkit-transform: translateY(-15px);
        transform: translateY(-15px)
    }
}

